import { createElement } from "react";
import { InformationName } from "./InformationName.fs.js";
import { Jotai_JotaiUtils } from "../Bindings/Jotai.fs.js";
import { Atoms_Task_information } from "../State.fs.js";

export function TaskInformationName(input) {
    return createElement(InformationName, {
        Information: Jotai_JotaiUtils.useAtomValue(Atoms_Task_information([input.Username, input.TaskId])),
        Username: input.Username,
    });
}

