import { Jotai_JotaiUtils } from "../Bindings/Jotai.fs.js";
import { Atoms_User_UIFlagType, Atoms_User_uiFlag } from "../State.fs.js";
import { box } from "../Bindings/Chakra.fs.js";

export function InformationForm(input) {
    const informationUIFlag = Jotai_JotaiUtils.useUpdateAtom(Atoms_User_uiFlag([input.Username, new Atoms_User_UIFlagType(1)]));
    return box()((x) => {
        x.padding = "15px";
    })(["No information selected"]);
}

